<template>
  <v-app>
    <v-content>
      <v-container>
        <v-snackbar
          v-model="installDialog"
          vertical
          color="white"
          :timeout="installDialogTimeout"
          ><p color="black">Add two Home screen</p>
          <v-btn color="green" dark block rounded @click="installApp()"
            >Yes</v-btn
          >
          <v-btn color="red" text block rounded @click="disMissInstall()"
            >No</v-btn
          >
        </v-snackbar>
        <v-row justify="center">
          <v-col cols="12" md="6">
            <v-card class="pa-3">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="todo.title"
                    label="what To do?"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="todo.content"
                    label="content"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-btn color="success" block @click="saveToDo()">
                    Validate
                  </v-btn>
                </v-col>
              </v-row>
              <div v-if="todos.length">
                <hr />
                <v-list flat subheader three-line>
                  <v-subheader>TODO List</v-subheader>

                  <v-list-item-group multiple active-class="">
                    <div v-for="(todo, index) in todos" :key="index">
                      <v-list-item>
                        <template v-slot:default="{ active }">
                          <v-list-item-action>
                            <v-checkbox
                              v-model="todo.active"
                              :input-value="todo.active"
                            ></v-checkbox>
                          </v-list-item-action>

                          <v-list-item-content>
                            <v-list-item-title>{{
                              todo.title
                            }}</v-list-item-title>
                            <v-list-item-subtitle>{{
                              todo.content
                            }}</v-list-item-subtitle>
                          </v-list-item-content>
                        </template>
                      </v-list-item>
                    </div>
                  </v-list-item-group>
                </v-list>
                <v-btn color="red" block @click="removeToDo()"> remove </v-btn>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    todo: {
      title: "",
      content: "",
      active: false,
    },
    todos: [],
    installDialog: false,
    installDialogTimeout: 10000,
    deferredPrompt: null,
  }),
  watch: {
    todos: {
      handler() {
        localStorage.setItem("todos", JSON.stringify(this.todos));
      },
    },
  },
  methods: {
    saveToDo() {
      if (this.todo.title != "") {
        this.todos.push(this.todo);
        this.todo = {
          title: "",
          content: "",
          active: false,
        };
      }
    },
    removeToDo() {
      this.todos.forEach((todo, index) => {
        if (todo.active === true) {
          this.todos.splice(index);
        }
      });
    },
    installApp() {
      this.installDialog = false;
      this.deferredPrompt.prompt();
      this.deferredPrompt.userChoice.then((choiceResult) => {
        this.installDialog = false;
        this.deferredPrompt = null;
      });
    },
    disMissInstall() {
      this.installDialog = false;
    },
  },
  mounted() {
    window.addEventListener("beforeinstallpromt", (e) => {
      e.preventDefault();
      this.deferredPrompt = e;
      if (this.isMobileCheck) {
        this.installDialog = true;
      }
    });
    if (localStorage.getItem("todos")) {
      this.todos = JSON.parse(localStorage.getItem("todos"));
    }
  },
};
</script>
